import React, { FC, useCallback, useContext, useEffect, useState } from 'react';
import { Button, Form, Input, Table } from 'antd';
import { Link, RouteComponentProps } from '@reach/router';
import Ctx from '../Nav/ctx';
import * as Api from '../server/space';
import { Space } from '../server/vo';

const { Item } = Form;
const layout = {
  labelCol: { span: 8 },
  wrapperCol: { span: 16 },
};
const List: FC<RouteComponentProps> = () => {
  const ctx = useContext(Ctx);
  const [list, setList] = useState<Space[]>([]);
  const [form] = Form.useForm();
  const trigger = useCallback(() => {
    const list = Api.list();
    setList(list);
    ctx.update();
  }, [ctx]);
  useEffect(() => {
    trigger();
  }, [trigger]);
  const toSubmit = useCallback(
    (values) => {
      console.log('values', values);
      if (values.id) {
        Api.update(values);
      } else {
        Api.add(values);
      }
      trigger();
    },
    [trigger],
  );
  return (
    <div style={{ display: 'flex' }}>
      <div style={{ flex: 1 }}>
        <div>
          <Button
            type="primary"
            onClick={() => {
              form.setFieldsValue({ alias: '', id: '' });
            }}
          >
            Êñ∞Â¢û
          </Button>
        </div>
        <Table
          rowKey="id"
          dataSource={list}
          columns={[
            { key: 'id', dataIndex: 'id', title: 'ID' },
            { key: 'alias', dataIndex: 'alias', title: 'ALIAS' },
            {
              key: '__op__',
              render(row) {
                return (
                  <div>
                    <Button
                      onClick={() => {
                        Api.del({ id: row.id });
                        trigger();
                      }}
                      type="dashed"
                    >
                      Âà†Èô§
                    </Button>
                    <Button
                      type="primary"
                      onClick={() => {
                        form.setFieldsValue(row);
                      }}
                    >
                      ‰øÆÊîπ
                    </Button>
                    <Link to={`/ace/tag/${row.id}`}>
                      <Button>ÁºñËæëTAG</Button>
                    </Link>
                  </div>
                );
              },
            },
          ]}
        ></Table>
      </div>
      <div style={{ flex: 1 }}>
        <h3>ÁºñËæëÂå∫</h3>
        <Form {...layout} form={form} onFinish={toSubmit}>
          <Item name="id" label="ID" required>
            <Input disabled></Input>
          </Item>
          <Item name="alias" label="ALIAS" required>
            <Input></Input>
          </Item>
          <Button type="primary" htmlType="submit">
            ‰øùÂ≠ò
          </Button>
        </Form>
      </div>
    </div>
  );
};

export default List;
